import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsComplexAmountExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-amount/ps-complex-amount.component.interfaces';
import { IOptionsPsComplexExchnageExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component.interfaces';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'app-request-qr-payment',
  templateUrl: './request-qr-payment.page.html',
  styleUrls: ['./request-qr-payment.page.scss'],
})
export class RequestQrPaymentPage extends OmniBasePage implements OnInit {

  private formGroup = new FormGroup({});
  qrCodeVO: any = {};
  cifInfo;

  public stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'request_qr_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['request_qr_code_step1'],
    group: this.formGroup,
    submitOptions: {
      group: this.formGroup,
      submitServiceUrl: '',
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.generateQRCode(response));
          });
        },
        executionClass: this,
        params: [this]
      },
      extraParams: {
        actionType: 'save'
      }
    },
    requestObject: this.qrCodeVO
  };

  qrCodeDetailsPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'qr_code_details_key',
    //iconName: 'document',
    expanded: true
  };

  qrCodeChargesPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'qr_code_charges_key',
    //iconName: 'document',
    expanded: true
  };

  accountNoOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'account_no_key',
    placeHolder: 'select_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    purpose: 'QR', //added by Marina TP:#1433069 to be able to distinguish QR account to set it's value based on user default QR account in ps-lookup-own-account component 
    requestObject: this.qrCodeVO
  };

  qrComplexAmountOption: IOptionsPsComplexAmountExposed = {
    currenciesOptions: {
      labelKey: 'currency_key',
      placeHolder: 'currency_key',
      group: this.formGroup,
      fcName: 'qrCurrency'
    },
    amountOptions: {
      labelKey: 'amount_key',
      placeHolder: 'enter_amount_key',
      fcName: 'transactionAmount',
      group: this.formGroup,
      decimalPoints: 3
    }
  };

  purposeOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'purpose_key',
    placeHolder: 'enter_purpose_key',
    group: this.formGroup,
    fcName: 'purpose'
  };

  chargesAccountListOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'charges_account_key',
    placeHolder: 'select_charges_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'chargesAccount',
    accountType: 'G',
    fromTo: null,
    requestObject: this.qrCodeVO,
    currency: '',
  };

  public complexExchangeOptions: IOptionsPsComplexExchnageExposed = {
    editableMode: false,
    showToCurrencyOptions: true,
    fromAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'fromCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'charges_amount_key',
        placeHolder: 'enter_charges_amount_key',
        fcName: 'chargesAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    toAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'exchange_currency_key',
        fcName: 'currency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'exchange_amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'toAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    requestObject: this.qrCodeVO,
    group: this.formGroup
  };

  constructor(private commonService: PsCommonService, private logger: LoggerService, private omniPull: OmniPullService) {
    super();
  }

  ngOnInit() {
    super.init();
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    this.commonProv.cancelClicked.next(false);
    this.omniPull.getParamValOf(CommonBussinessConstant.QR_CHARGES_AMOUNT).then(result => {
      if (result && result[CommonBussinessConstant.QR_CHARGES_AMOUNT]) {
        this.commonProv.setValInsideNestedObj(this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName, result[CommonBussinessConstant.QR_CHARGES_AMOUNT], this.qrCodeVO);
        this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName] = result[CommonBussinessConstant.QR_CHARGES_AMOUNT];
      }
    })
    this.commonProv.setValInsideNestedObj(this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName, this.cifInfo.companySettingsCO.baseCurrency, this.qrCodeVO);
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = this.cifInfo.companySettingsCO.baseCurrency;
    }, 1);

    //added by ghada, AZDB-332 for closing the screen on clicking cancel button
    this.commonProv.cancelClicked.subscribe(result => {
      if (result) {
        this.navigationServices.navigateBackward();
      }
    })
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.purposeOptions.fcName], 0);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName], 0);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.amountOptions.fcName], 1);

  }

  onChargesAccountChange(account) {
    this.complexExchangeOptions.toAmountOptions.currency = account.currencyBriefNameEnglish;
    this.complexExchangeOptions.toAmountOptions.currencyCode = account.currency;

    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName] = account.currency;
    }, 1);
  }

  generateQRCode(resp?) {
    const data = {
      AccountRef: this.formGroup.controls[this.accountNoOptions.fcName].value.accountFormat + '-' + this.formGroup.controls[this.accountNoOptions.fcName].value.formattedAccount,
      AddtionalRef: 'AddRef-' + this.formGroup.controls[this.accountNoOptions.fcName].value.additionalRef,
      purposeTxtDesc: this.formGroup.controls[this.purposeOptions.fcName].value,
      transactionAmount: this.formGroup.controls[this.qrComplexAmountOption.amountOptions.fcName].value,
      currency: this.formGroup.controls[this.qrComplexAmountOption.currenciesOptions.fcName].value,
      showToOtherDetails: true,
      toAccountType: 'companyAccountTransfer',
      changeScannedDataToRO: true
    };
    this.logger.log('this.stepperOptions.requestObject', this.stepperOptions.requestObject);
    this.commonService.generateQRCode(data, CommonBussinessConstant.QR_CODE_GENERATION_OPER_ID);

  }

  updateVO(resp) {
    PsCommonSettings.oper_ID = CommonBussinessConstant.QR_CODE_GENERATION_OPER_ID;
  }

}
